var errorTimeout = {
    componentDidUpdate : function() {
        if (this.state.error === true) {
            this.interval = setTimeout(function(){
                this.resetForm();
            }.bind(this), 5000);
        }
    },

    componentWillUnmount : function() {
        clearTimeout(this.interval);
    },

    resetForm : function() {
        clearTimeout(this.interval);
        this.setState({
            error : false
        });
    }
}

var ReactCSSTransitionGroup = React.addons.CSSTransitionGroup;
var swipeTimeout = null;

var Swipe = React.createClass({
    mixins: [errorTimeout],

    getInitialState: function() {
        return {
            error : 0,
            visitor : '',
        };
    },

    getDefaultProps: function() {
        return {
            handleClick : null
        };
    },

    loginFailure : function() {
        this.setState({
            visitor : '',
            error : 1
        });
        this.timedReset();
    },

    alreadyVisiting : function() {
        this.setState({
            visitor : '',
            error : 2
        });
    },

    timedReset : function() {
        swipeTimeout = setTimeout(function(){
            this.resetSwipe();
        }.bind(this), 4000);
    },

    resetSwipe : function() {
        clearTimeout(swipeTimeout);
        this.setState({
            error : 0,
            visitor : null,
        });
    },

    logInVisitor : function() {
        var visitor = this.state.visitor;
        if (visitor && visitor.length > 3) {
            if (visitor.charAt(0) === ';' && visitor.charAt(10) === '=') {
                visitor = visitor.slice(1,10);
            }

            $.getJSON('counseling/User/Checkin', {
            	command : 'loginVisitor',
                bannerId : visitor
            })
            .done(function(data){
                if(data.waiting !== undefined) {
                    this.alreadyVisiting();
                    this.timedReset();
                } else if (data.visitor === null) {
                    this.loginFailure();
                } else {
                    this.props.update(data);
                }
            }.bind(this))
            .fail(function() {
                this.loginFailure();
            }.bind(this));
        } else {
            this.loginFailure();
        }
    },

    handleChange : function(e) {
        var character = e.target.value;
        this.setState({
            visitor : character
        });
    },

    submitVisitor : function(e)
    {
        e.preventDefault();
        this.logInVisitor();
    },

    focusSwiper : function() {
        $('#swiper').focus();
    },

    componentDidUpdate: function(prevProps, prevState) {
        this.focusSwiper();
    },

    componentDidMount: function(prevProps, prevState) {
        this.focusSwiper();
    },

    render: function() {
        var field = null;
        var button = null;

        if (this.state.error === 1) {
            field = (
                <div className="text-center">
                    <div className="alert alert-danger alert-dismissible" role="alert" ref="errorAlert">
                        Account not found. Please try again or see the front desk.
                    </div>
                    <button className="btn btn-default" type="button" onClick={this.resetSwipe}><i className="fa fa-repeat"></i> Try again</button>
                </div>
            );
        } else if (this.state.error === 2) {
            field = (
                <div className="alert alert-warning alert-dismissible" role="alert" ref="errorAlert">
                    <button className="close" type="button" onClick={this.resetSwipe}><i className="fa fa-times"></i></button>
                    You are already logged in. Please visit the front desk if you have a question or concern.
                </div>
            );
        } else {
            field = <input id="swiper" type="text" placeholder="Banner ID" onChange={this.handleChange}
                 className="form-control" value={this.state.visitor} />;
            button = <button className="continue pull-right btn btn-default" onClick={this.logInVisitor}>Continue <i className="fa fa-chevron-right fa-sm"></i></button>;
        }

        var content = (
            <div>
                <div className="text-center">
                    <p className="title">Welcome! Please Check-in</p>
                    <p className="subtitle">Swipe your AppCard to get started</p>
                    <p>Don't have your AppCard?<br />Enter your Banner ID number instead.</p>
                    <form onSubmit={this.submitVisitor}>
                    {field}
                    </form>
                </div>
                {button}
                <div className="clearfix"></div>
            </div>
        );
        return (
            <Box content={content}/>
        );
    }

});

var Reason = React.createClass({
    getInitialState: function() {
        return {
            reasons : null,
        };
    },

    getDefaultProps: function() {
        return {
            update: null,  // function to run upon reason selection
            visitor : null //visitor object
        };
    },

    componentDidMount: function() {
        $.getJSON('counseling/User/Reason', {
        	command : 'list'
        }).done(function(data){
            if (data === null) {
                data = [];
            }
            this.setState({
                reasons : data
            })
        }.bind(this));

    },

    pickReason : function(key)
    {
        this.props.update(this.state.reasons[key]);
    },

    render: function() {
        var content;

        if (this.state.reasons === null) {
            content = 'Loading...';
        } else if (this.state.reasons.length === 0 ){
            content = <div className="alert alert-danger">System error: Please alert front desk.</div>;
        } else {
            var reasonList = this.state.reasons.map(function(value, i){
                return <li key={i} className="list-group-item" style={{cursor : 'pointer'}} onClick={this.pickReason.bind(this, i)}>{value.description}</li>;
            }.bind(this));
            content = (
                <div>
                    <div className="text-center">
                        <p className="title">Hello, {this.props.visitor.preferred_name}.</p>
                        <p className="title">Why are you visiting today?</p>
                    </div>
                    <ul className="list-group">
                        {reasonList}
                    </ul>
                </div>
            );
        }

        return (
            <Box content={content}/>
        );
    }

});

var Phone = React.createClass({
    mixins: [errorTimeout],

    getInitialState: function() {
        return {
            phoneNumber : '',
            error : false
        };
    },

    getDefaultProps: function() {
        return {
            update : null,
            visitor : null,
            back : null
        };
    },

    componentDidMount : function() {
        this.setState({
            phoneNumber : this.props.visitor.phone_number
        });
    },

    updatePhone : function() {
        if (this.state.phoneNumber.length < 10) {
            this.setState({error:true});
        } else {
            this.props.update(this.state.phoneNumber);
        }
    },

    handleChange : function(e) {
        this.setState({
            phoneNumber : e.target.value
        });
    },

    render: function() {
        var field = null;
        if (this.state.error) {
            field = (
                <div className="alert alert-danger alert-dismissible" role="alert" ref="errorAlert">
                    <button className="close" type="button" onClick={this.resetForm}><i className="fa fa-times"></i></button>
                    Please enter your cell phone number including your area code.
                </div>
            );
        } else {
            field = <input type="text" ref="phone" className="form-control" placeholder="Cell phone number with area code"
                value={this.state.phoneNumber} onChange={this.handleChange}/>
        }

        var content =  (
            <div>
                <div className="text-center">
                    <p className="title">Ok, {this.props.visitor.preferred_name}.</p>
                    <p className="subtitle">In case we need to reach you later,<br />
                        please enter your cell phone number.</p>
                    {field}
                    <button className="continue pull-left btn btn-default" onClick={this.props.back}><i className="fa fa-chevron-left fa-sm"> Back </i></button>
                    <button className="continue pull-right btn btn-default" onClick={this.updatePhone}>Continue <i className="fa fa-chevron-right fa-sm"></i></button>
                    <div className="clearfix"></div>
                </div>
            </div>
        );

        return (
            <Box content={content}/>
        );

    }

});

var Emergency = React.createClass({
    getDefaultProps: function() {
        return {
            update : null
        };
    },

    hasEmergency: function(answer) {
        this.props.update(answer);
    },


    render: function() {
        var content = (
            <div>
                <div className="text-center">
                    <p className="title">A few quick questions</p>
                    <p className="subtitle">Are you currently experiencing an emergency?</p>
                    <p style={{textAlign : 'left'}}>Examples of emergencies include recent:</p>
                    <ul>
                        <li>suicidal crisis</li>
                        <li>sexual assault</li>
                        <li>homicidal thoughts</li>
                        <li>death of a friend or loved one</li>
                        <li>unusual experiences such as hearing voices or seeing things other people do not</li>
                        <li>serious accident</li>
                        <li>other similar events</li>
                    </ul>
                    <button style={{marginRight:'1em',width: '100px'}} className="btn btn-default btn-lg" onClick={this.hasEmergency.bind(this, true)}>Yes</button>
                    <button style={{width: '100px'}}className="btn btn-default btn-lg" onClick={this.hasEmergency.bind(this, false)}>No</button>
                </div>
                <div className="clearfix"></div>
            </div>
        );
        return (
            <Box content={content}/>
        );
    }

});

var Instruction = React.createClass({
    getInitialState: function() {
        return {
            instructions : null
        };
    },

    getDefaultProps: function() {
        return {
            instruction : '1',
            instructionList : null,
            reset : null
        };
    },

    render: function() {
        var content = null;
        var instruction = null

        if (this.props.instruction === '1') {
            instruction = this.props.instructionList['1'];
        } else {
            instruction = this.props.instructionList['2'];
        }

        content = (
            <div>
                <div className="text-center">
                    <p className="title">Ok, you're checked in.</p>
                    <p className="subtitle">{instruction}</p>
                </div>
                <div className="text-right"><button className="btn btn-default" onClick={this.props.reset}>Finished</button></div>
            </div>
        );

        return (
            <Box content={content}/>
        );
    }

});

'use strict';

var resetTimeout = null;

var Login = React.createClass({
    getInitialState: function() {
        return {
            stage : 'swipe'
        };
    },

    updateStage : function(stage) {
        this.setState({
            stage : stage
        });
    },

    render: function() {
        return (
            <Stage updateStage={this.updateStage} stage={this.state.stage}/>
        );
    }

});

var Stage = React.createClass({
    getInitialState: function() {
        return {
            visitor : null,
            reason : null,
            phone : null,
            emergency : false,
            instructionList : null
        };
    },

    getDefaultProps: function() {
        return {
            stage : 'swipe'
        };
    },

    componentDidMount : function() {
        $.getJSON('counseling/User/Checkin', {
        	command : 'instructions'
        }).done(function(data){
            this.setState({
                instructionList : data
            });
        }.bind(this));

    },

    updateReason : function(reason) {
        this.setState({
            reason : reason
        });
        if (reason.ask_for_phone === '1') {
            this.props.updateStage('phone');
        } else {
            if (reason.show_emergency === '1') {
                this.props.updateStage('emergency');
            } else {
                this.props.updateStage('instruction')
            }
        }
    },

    updateVisitor : function(visitor) {
        this.setState({
            visitor : visitor,
        });
        this.props.updateStage('reason');
    },

    updatePhone : function(phone) {
        if (phone === null || phone.length === 0) {
            return;
        }
        //update phone number if changed
        if (phone !== this.state.visitor.phone_number) {
            $.post('counseling/User/Visitor', {
            	command : 'updatePhone',
                visitorId : this.state.visitor.id,
                phoneNumber : phone
            }, null, 'json')
            	.done(function(data){
            		//console.log(data);
            	}.bind(this));

        }

        this.setState({
            phone : phone
        });
        if (this.state.reason.show_emergency === '1') {
            this.props.updateStage('emergency');
        } else {
            this.props.updateStage('instruction')
        }
    },

    updateEmergency : function(emergency) {
        this.setState({
            emergency : emergency
        });
        this.props.updateStage('instruction');
    },


    componentDidUpdate : function(props, state)
    {
        if (this.props.stage === 'instruction') {
            this.completeCheckin();
        }
    },

    completeCheckin : function() {
        $.post('counseling/User/Visit', {
        	command : 'create',
            visitorId : this.state.visitor.id,
            reasonId : this.state.reason.id,
            emergency : this.state.emergency
        }, null, 'json')
        	.done(function(data){
                resetTimeout = setTimeout(this.resetLogin, 5000);
        	}.bind(this));
    },

    resetLogin : function() {
        clearTimeout(resetTimeout);
        this.props.updateStage('swipe');
        this.setState({
            visitor : null,
            reason : null,
            phone : null,
            emergency : false,
        });
    },

    backToReason : function() {
        this.setState({
            reason : null
        });
        this.props.updateStage('reason');
    },

    render: function() {
        var content = null;
        switch (this.props.stage) {
            case 'swipe':
                return <Swipe update={this.updateVisitor} />;
            break;

            case 'reason':
                return <Reason update={this.updateReason} visitor={this.state.visitor}/>;
            break;

            case 'phone':
                return <Phone update={this.updatePhone} visitor={this.state.visitor} back={this.backToReason}/>;
            break;

            case 'emergency':
                return <Emergency update={this.updateEmergency}/>;
            break;

            case 'instruction':
                return <Instruction reset={this.resetLogin} instruction={this.state.reason.instruction} instructionList={this.state.instructionList}/>
            break;
        }
    }

});

var Box = React.createClass({

    getDefaultProps: function() {
        return {
            content : 'Empty'
        };
    },

    render: function() {
        return (
            <div className="checkin-box">{this.props.content}</div>
        );
    }

});


ReactDOM.render(<Login/>, document.getElementById('Login'));
